m;\r\n}\r\n\r\n/*\r\n================\r\n     BODY\r\n================\r\n*/\r\n\r\n.body-content-wrapper {\r\n  display: grid;\r\n  grid-template-columns: 1fr 1.5fr;\r\n  /* khoảng cách giữa 2 cột */\r\n  grid-gap: 1rem;\r\n  padding: 2rem;\r\n}\r\n\r\n/* phần twitter */\r\n\r\n.twitter-feed-container {\r\n  margin-top: 1rem;\r\n  width: 100%;\r\n}\r\n\r\n.twitter-feed-body {\r\n  display: flex;\r\n  width: 100%;\r\n  height: 100%;\r\n  flex-flow: column;\r\n}\r\n\r\n.twitter-loader {\r\n  border: 3px solid hsla(185, 100%, 62%, 0.2);\r\n  border-top-color: #3cefff;\r\n  border-radius: 50%;\r\n  width: 3em;\r\n  height: 3em;\r\n  -webkit-animation: spin 1s linear infinite;\r\n          animation: spin 1s linear infinite;\r\n  justify-self: center;\r\n  align-self: center;\r\n  margin-top: 3rem;\r\n}\r\n\r\n@-webkit-keyframes spin {\r\n  to {\r\n    transform: rotate(360deg);\r\n  }\r\n}\r\n\r\n@keyframes spin {\r\n  to {\r\n    transform: rotate(360deg);\r\n  }\r\n}\r\n\r\n.twitter-no-tweets-text {\r\n  margin-top: 3rem;\r\n  text-align: center;\r\n}\r\n\r\n.twitter-feed-text {\r\n  color: #0c1066;\r\n  font-size: 1.25rem;\r\n}\r\n\r\n.twitter-icon {\r\n  width: 1.5rem;\r\n}\r\n\r\n.twitter-feed-tag-text {\r\n  font-size: 0.85rem;\r\n  color: #5f84fb;\r\n  letter-spacing: 0.1rem;\r\n  text-transform: uppercase;\r\n}\r\n\r\n.twitter-feed-header {\r\n  display: grid;\r\n  grid-template-rows: 2rem;\r\n  grid-template-columns: 0.5fr 1.5fr 1fr;\r\n  align-items: center;\r\n  justify-items: center;\r\n  width: 100%;\r\n}\r\n\r\n.twitter-tweet-container {\r\n  margin-top: 2rem;\r\n}\r\n\r\n.tweet-user-wrapper {\r\n  display: flex;\r\n  margin-bottom: 0.5rem;\r\n}\r\n\r\n.tweet-avatar-image {\r\n  margin-right: 0.5rem;\r\n  border-radius: 50%;\r\n  height: 3rem;\r\n  width: 3rem;\r\n}\r\n\r\n.tweet-username-text {\r\n  display: block;\r\n  font-weight: bold;\r\n}\r\n\r\n.tweet-text {\r\n  margin-top: 0\r\n}\r\n\r\n/* forecast-container */\r\n\r\n.forecast-container {\r\n  /